<template>
    <div class="box">
        <div class="wand-preset" v-for="val in wandPresets" :key="val.stats.name">
            <span class="label">{{val.stats.name}}</span>
            <WandSlot :value="val.stats.sprite" />
            <span class="label">Always Cast</span>
            <div class="is-inlined" v-for="spell in val.alwaysCast" :key="genKey(spell)">
                <SpellSlot :value="spell" />
            </div>
            <span class="label">Spells</span>
            <div class="is-inlined" v-for="spell in val.deck" :key="genKey(spell)">
                <SpellSlot :value="spell" />
            </div>
            <div class="column">
                <div class="is-centered buttons">
                    <b-button @click="alert(val)" type="is-primary" size="is-medium">Get Code</b-button>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import spellData from "../assets/spellData.json";
import wandsData from "../assets/wandsData.json";
import SpellSlot from "./SpellSlot.vue";
import WandSlot from "./WandSlot.vue";
export default {
    components: {
        SpellSlot,
        WandSlot
    },
    data() {
        return {
            spells: spellData,
            wands: wandsData,
            wandPresets: {}
        };
    },
    beforeMount() {
        let presets = localStorage.getItem("wands");
        if (presets) {
            this.wandPresets = JSON.parse(presets);
        }
    },
    methods: {
        genKey(spell) {
            return `${spell.id}_${Math.random() * 10000}`;
        },
        getWandCode(wand) {
            let tiny = [
                [
                    wand.stats.sprite.name,
                    Number(wand.stats.shuffle),
                    wand.stats.spellsCast,
                    wand.stats.cast,
                    wand.stats.recharge,
                    wand.stats.capacity,
                    wand.stats.spread
                ],
                [
                    wand.alwaysCast[0].i
                ]
            ]
            let deck = wand.deck.map(v => v.i)
            tiny.push(deck)
            return JSON.stringify(tiny)
        },
        alert(wand) {
            this.$buefy.dialog.alert({
                title: "Copy the code below",
                message: this.getWandCode(wand),
                confirmText: "Done."
            });
        }
    }
};
</script>